<template>
    <section class="ui-container">
        <h1 class="title">DIALOG</h1>
        <div class="demo-item">
            <p class="demo-desc">对话框</p>
            <div class="demo-block">
                <!-- 带标题文字消息 -->
                <vf-dialog v-if="show1" @close="show1=false">
                    <div class="ui-dialog-bd">
                        <h3>下线通知</h3>
                        <p>你的账号于17:00在一台Android手机登录。如非本人操作，密码可能已泄露，建议立即冻结账号。</p>
                    </div>
                </vf-dialog>
                <!-- 无标题文字消息 -->
                <vf-dialog v-if="show2" @close="show2=false">
                    <div class="ui-dialog-bd">
                        <p>将终止文件发送，确定返回？</p>
                    </div>
                </vf-dialog>
                <!-- 结构化消息 -->
                <vf-dialog :media="true" v-if="show3" @close="show3=false">
                    <div class="ui-dialog-bd">
                        <h3>发送到 seame芝麻</h3>
                        <div class="ui-dialog-item">
                            <div class="ui-img">
                                <span style="background-image:url(http://placeholder.qiniudn.com/140x140)"></span>
                            </div>
                            <div class="ui-dialog-info">
                                <h5 class="ui-nowrap">这是标题，加ui-nowrap可以超出长度截断</h5>
                                <p class="ui-nowrap">这是内容，加ui-nowrap可以超出长度截断</p>
                                <span class="ui-nowrap">应用 开眼</span>
                            </div>
                        </div>
                    </div>
                </vf-dialog>
                <!-- 通用运营对话框－运营图展示类 -->
                <vf-dialog :operate="true"  v-if="show4" @close="show4=false">
                    <div class="ui-dialog-cnt">
                        <div class="ui-dialog-hd">
                            <div class="ui-img">
                                <span style="background-image:url(http://placeholder.qiniudn.com/640x300)"></span>
                            </div>
                        </div>
                        <div class="ui-dialog-bd">
                            <h3>使用QQ钱包优惠购</h3>
                            <p>参加天猫运动会，使用QQ钱包购买智能穿戴！分期0首付0手续费，放肆去浪！</p>
                        </div>
                        <div class="ui-dialog-ft">
                            <button class="ui-btn-lg">立即查看</button>
                        </div>
                        <i class="ui-dialog-close" @click="show4=false"></i>
                    </div>
                </vf-dialog>
                <vf-dialog :operate="true"  v-if="show5">
                    <div class="ui-dialog-hd">
                        <div class="ui-img">
                            <span style="background-image:url(http://placeholder.qiniudn.com/640x300)"></span>
                        </div>
                    </div>
                    <div class="ui-dialog-bd">
                        <p>按钮颜色根据运营氛围需要自行重置！</p>
                    </div>
                    <div class="ui-dialog-ft">
                        <button type="button" data-role="button" class="ui-btn">查看详情</button>
                        <button type="button" data-role="button" class="ui-btn">下个任务</button>
                    </div>
                    <i class="ui-dialog-close" @click="show5=false"></i>
                </vf-dialog>
                <!-- 通用运营对话框－图标展示类 -->
                <vf-dialog :operate="true" v-if="show6">
                    <div class="ui-dialog-hd">
                        <div class="ui-img">
                            <span style="background-image:url(http://placeholder.qiniudn.com/420x220)"></span>
                        </div>
                    </div>
                    <div class="ui-dialog-bd">
                        <h3>恭喜你获得10个专属福袋！</h3>
                    </div>
                    <div class="ui-dialog-ft">
                        <button class="ui-btn-lg">发福袋</button>
                    </div>
                    <i class="ui-dialog-close" @click="show6=false"></i>
                </vf-dialog>
            </div>
            <div class="ui-btn-wrap">
                <vf-button size="lg" @onClick="show1=true">文字提示(带标题)</vf-button>
            </div>
            <div class="ui-btn-wrap">
                <vf-button size="lg" @onClick="show2=true">文字提示(不带标题)</vf-button>
            </div>
            <div class="ui-btn-wrap">
                <vf-button size="lg" @onClick="show3=true">结构化消息提示</vf-button>
            </div>
            <div class="ui-btn-wrap">
                <vf-button size="lg" @onClick="show4=true">运营提示(运营图展示类)(单按钮)</vf-button>
            </div>
            <div class="ui-btn-wrap">
                <vf-button size="lg" @onClick="show5=true">运营提示(运营图展示类)(双按钮)</vf-button>
            </div>
            <div class="ui-btn-wrap">
                <vf-button size="lg" @onClick="show6=true">运营提示(图标展示类)</vf-button>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    data () {
        return {
            show1: false,
            show2: false,
            show3: false,
            show4: false,
            show5: false,
            show6: false
        }
    }
}
</script>



