<template>
    <section class="ui-container">
        <h1 class="title">ACTIONSHEET</h1>
        <div class="demo-item">
            <p class="demo-desc">菜单</p>
        </div>
        <div class="ui-btn-wrap">
            <vf-button size="lg" @onClick="show=true">Actionsheet</vf-button>
        </div>
        <vf-actionsheet v-if="show" @close="show=false" title="同时会将我从对方的列表中删除">
            <button>分享</button>
            <button class="del">删除</button>
        </vf-actionsheet>
    </section>
</template>

<script>
export default {
    data () {
        return {
            show: false
        }
    }
}
</script>



